








import { Component, Vue, Prop } from 'vue-property-decorator';

@Component
export default class Board extends Vue {
  @Prop() private blocks!: string;

  private get getBlocks() {
    let result = [];
    try {
      result = JSON.parse(this.blocks);
    } catch (e) {
      console.log(e);
    }
    return result;
  }
}
